/* eslint-disable */

const tokens = {
  admin: 'admin-token',
  guest: 'guest-token'
}

const admin = {
  id: 'admin-id',
  name: 'administrator',
  avatar: 'https://gw.alipayobjects.com/zos/rmsportal/BiazfanxmamNRoxxVxka.png',
  menus: [
    { path: '/list', title: '' },
    { path: '/list/content', title: '' },
    { path: '/list/content/article', title: '' },
    { path: '/list/content/project', title: '' },
    { path: '/list/content/application', title: '' },
    { path: '/list/basic', title: '' },
    { path: '/form', title: '' },
    { path: '/form/step', title: '' },
    { path: '/form/basic', title: '' },
    { path: '/form/advanced', title: '' }
  ],
  buttons: [
    {
      path: '/list/basic',
      type: 'search'
    },
    {
      path: '/list/basic',
      type: 'add'
    }
  ]
}

const guest = {
  id: 'guest-id',
  name: 'guest',
  avatar: 'https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png',
  menus: [
    { path: '/list', title: '' },
    { path: '/list/content', title: '' },
    { path: '/list/content/article', title: '' },
    { path: '/list/content/project', title: '' },
    { path: '/list/basic', title: '' },
    { path: '/form', title: '' },
    { path: '/form/step', title: '' },
    { path: '/form/basic', title: '' }
  ],
  buttons: [
    {
      path: '/list/basic',
      type: 'search'
    }
  ]
}

export default [
  {
    path: '/user/login',
    method: 'post',
    response: options => {
      const body = JSON.parse(options.body)
      const token = tokens[body.account]
      if (!token || body.password !== '123456') {
        return { code: 3000, message: '' }
      }
      return { code: 2000, data: { token }, message: '' }
    }
  },
  {
    path: '/user/info',
    method: 'get',
    response: options => {
      const token = localStorage.getItem('Token')
      if (token === tokens.admin) {
        return { code: 2000, data: admin, message: '' }
      }
      return { code: 2000, data: guest, message: '' }
    }
  }
]
