
import { Component, Watch, Prop, PropSync, Vue } from 'vue-property-decorator'
import { hasClassName } from '@/utils/dom'

@Component
export default class extends Vue {
  $refs!: {
    drawer: HTMLElement
  }

  @Prop({ default: '30%' }) readonly width!: string // 
  @Prop({ default: false }) readonly fixed!: boolean // fixed
  @Prop({ default: true }) readonly showClose!: boolean // 
  @Prop({ default: true }) readonly modal!: boolean // 
  @Prop({ default: true }) readonly wrapperClosable!: boolean // 
  @Prop({ default: false }) readonly appendToBody!: boolean // body
  @Prop({ default: true }) readonly lockScroll!: boolean // 
  @PropSync('visible', { type: Boolean, default: false }) syncedVisible!: boolean

  @Watch('syncedVisible')
  onVisibleChange (newValue: boolean): void {
    if (newValue) {
      this.addHiddenClass()
    } else {
      this.removeHiddenClss()
    }
  }

  mounted (): void {
    if (this.syncedVisible) {
      this.addHiddenClass()
    }
    if (this.appendToBody) {
      document.body.appendChild(this.$el)
    }
  }

  beforeDestroy (): void {
    if (this.lockScroll) {
      this.removeHiddenClss()
    }
    if (this.appendToBody && this.$el && this.$el.parentNode) {
      this.$el.parentNode.removeChild(this.$el)
    }
  }

  addHiddenClass (): void {
    if (!this.lockScroll) return

    const el: HTMLElement | null = this.$refs.drawer.parentElement
    if (el) {
      !hasClassName(el, 'drawer-lock-scroll') && el.classList.add('drawer-lock-scroll')
    }
  }

  removeHiddenClss (): void {
    if (!this.lockScroll) return

    const el: HTMLElement | null = this.$refs.drawer.parentElement
    if (el) {
      hasClassName(el, 'drawer-lock-scroll') && el.classList.remove('drawer-lock-scroll')
    }
  }

  handleClickMask (): void {
    if (this.wrapperClosable) {
      this.onClose()
    }
  }

  onClose (): void {
    this.syncedVisible = false
  }
}
